import React, {DetailedHTMLProps, SelectHTMLAttributes, useState} from 'react'
import styles from './ComboBox.module.css'

// тип пропсов обычного инпута
type DefaultSelectPropsType = DetailedHTMLProps<SelectHTMLAttributes<HTMLSelectElement>, HTMLSelectElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type ComboBoxPropsType = DefaultSelectPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    items?: Array<string>
    itemIndex?: number
    width?: number
}

export const ComboBox: React.FC<ComboBoxPropsType> = (props) => {
    const {
        items = [],
        itemIndex = 0,
        width = 0,
        style = {},

        onMouseDown,
        onKeyDown,
        onBlur,
        onChange,

        ...restProps
    } = props

    const [index, setIndex] = useState(itemIndex)
    const [selected, setSelected] = useState(itemIndex > 0 ? true : false)

    const selectStyle = {
        minWidth: width,
    }

    const onMouseKeyDown = () => {
        if (index < 0) {
            setIndex(0)
        } else {
            !selected && setIndex(-1)
        }
    }

    return (
        <select
            style={{...style, ...selectStyle}}
            value={index}

            onMouseDown={(e) => {
                onMouseKeyDown()
                onMouseDown && onMouseDown(e)
            }}

            onKeyDown={(e) => {
                const code = e.code
                if (code === 'Enter' || code === 'Space') {
                    onMouseKeyDown()
                }
                onKeyDown && onKeyDown(e)
            }}

            onBlur={(e) => {
                !selected && setIndex(-1)
                onBlur && onBlur(e)
            }}

            onChange={(e) => {
                setSelected(true)
                setIndex(+e.currentTarget.value)
                onChange && onChange(e)
            }}

            {...restProps}
        >
            {(index >= 0) && items.map((item, i) =>
                <option key={i} value={i}>
                    {item}
                </option>
            )}
        </select>
    )
}