import React, {DetailedHTMLProps, HTMLAttributes, ReactNode} from 'react'
import styles from './GroupBox.module.css'

// тип пропсов обычного инпута
type DefaultDivPropsType = DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type GroupBoxPropsType = DefaultDivPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
}

export const GroupBox: React.FC<GroupBoxPropsType> = (props) => {
    const {
        children,
        ...restProps
    } = props

    return (
        <div
            className={styles.block}
            {...restProps}
        >
            {props.children}
        </div>
    )
}