import React, {useState} from 'react';
import './App.css';
import {ShowComponent} from './utils/ShowComponent/ShowComponent';
import {ShowResult} from './utils/ShowResult/ShowResult';
import {Label} from './components/Label/Label';
import {Edit} from './components/Edit/Edit';
import {CheckBox} from './components/CheckBox/CheckBox';
import {BitBtn} from './components/BitBtn/BitBtn';
import {Memo} from './components/Memo/Memo';
import {ComboBox} from './components/ComboBox/ComboBox';
import {GroupBox} from './components/GroupBox/GroupBox';
import {DateTimeEdit} from './components/DateTimeEdit/DateTimeEdit';
import {DatePicker} from './components/DatePicker/DatePicker';
import {DateEdit} from './components/DateEdit/DateEdit';
import { ModalWindowAtElement } from './components/ModalWindowAtElement/ModalWindowAtElement';
import {DatePickerTable} from './components/DatePickerTable/DatePickerTable';


function App() {
    const [edit1, setEdit1] = useState('Edit text with Title1')
    const [edit2, setEdit2] = useState('Edit text without Title2')

    const [checkBox1, setCheckBox1] = useState(false)
    const [checkBox2, setCheckBox2] = useState(false)

    const [memo1, setMemo1] = useState(['Memo text1', 'Memo text2'])

    const comboBoxItems1 = ['line1', 'line2', 'line3', 'line4', 'line5', 'line6', 'line7', 'line8', 'line9']
    const [comboBoxItemIndex1, setComboBoxItemIndex1] = useState(-1)
    const comboBoxItems2 = ['line12', 'line22', 'line32', 'line42', 'line52', 'line62', 'line72', 'line82', 'line92']
    const [comboBoxItemIndex2, setComboBoxItemIndex2] = useState(4)

    // const [date1, setDate1] = useState(new Date(2022, 1, 25))
    const [date1, setDate1] = useState(new Date(2021, 11, 28))
    const [date2, setDate2] = useState(new Date(1981, 11, 2))
    const [date3, setDate3] = useState(new Date(2021, 11, 31))


    return (
        <div className="App">

            <ShowComponent name={'DateEdit'}>
                <DateEdit
                    initDate={date2}
                    getDate={setDate2}
                />
                <p/>
                <DateEdit
                    initDate={date2}
                    getDate={setDate2}
                    showBorder={false}
                />

                <ShowResult
                    result={date2.toLocaleString('default', {day: '2-digit', month: 'long', year: 'numeric'})}/>
            </ShowComponent>



            <ShowComponent name={'DatePickerTable'}>
                    <DatePickerTable
                        initDate={date3}
                        initSize={[2,3]}
                        getDate={setDate3}
                    />

                <ShowResult
                    result={date3.toLocaleString('default', {day: '2-digit', month: 'long', year: 'numeric'})}/>
            </ShowComponent>



            <ShowComponent name={'DatePicker'}>
                <div style={{display: 'flex'}}>
                    <DatePicker
                        initDate={date1}
                        getDate={setDate1}
                        showFooterButtons={true}
                        monthBoardButtonDown={() => alert('DOWN button pressed!')}
                    />
                    <DatePicker
                        previousMonthButton={false}
                        // nextMonthButton={false}
                        getDate={setDate1}
                    />
                </div>

                <div style={{display: 'flex'}}>
                    <DatePicker
                        previousMonthButton={false}
                        nextMonthButton={false}
                        getDate={setDate1}
                    />
                    <DatePicker
                        previousMonthFunction={() => alert('PreviousMonth button pressed!')}
                        nextMonthButton={false}
                        showFooterContent={false}
                        getDate={setDate1}
                    />
                </div>

                <ShowResult
                    result={date1.toLocaleString('default', {day: '2-digit', month: 'long', year: 'numeric'})}/>
            </ShowComponent>


            {/*<ShowComponent name={'DateTimeEdit'}>*/}
            {/*    <DateTimeEdit/>*/}
            {/*</ShowComponent>*/}

            <hr style={{margin: '30px 0'}}/>


            <ShowComponent name={'Label'}>
                <Label caption={'Label text'}/>
            </ShowComponent>


            <ShowComponent name={'Edit'}>
                <Edit
                    title={' Edit with title RightJustify'}
                    textAlignmentRight={true}
                    text={edit1}
                    onChange={(e) => setEdit1(e.currentTarget.value)}
                />
                <ShowResult result={edit1}/>
                <hr color={'green'}/>

                <Edit
                    title={'Edit with title LeftJustify '}
                    textAlignmentRight={false}
                    text={edit1}
                    onChange={(e) => setEdit1(e.currentTarget.value)}
                />
                <ShowResult result={edit1}/>
                <hr color={'green'}/>

                <Edit
                    text={edit2}
                    onChange={(e) => setEdit2(e.currentTarget.value)}
                />
                <ShowResult result={edit2}/>
            </ShowComponent>


            <ShowComponent name={'CheckBox'}>
                <CheckBox
                    caption={'CheckBox RightJustify'}
                    textAlignmentRight={true}
                    checked={checkBox1}
                    onChange={(e) => setCheckBox1(e.currentTarget.checked)}
                />
                <ShowResult result={checkBox1 ? 'checked' : 'unChecked'}/>
                <hr color={'green'}/>

                <CheckBox
                    caption={'CheckBox LeftJustify'}
                    textAlignmentRight={false}
                    checked={checkBox2}
                    onChange={(e) => setCheckBox2(e.currentTarget.checked)}
                />
                <ShowResult result={checkBox2 ? 'checked' : 'unChecked'}/>
            </ShowComponent>


            <ShowComponent name={'BitBtn'}>
                <BitBtn
                    caption={'BitButton with Image'}
                    image={'https://upload.wikimedia.org/wikipedia/commons/thumb/a/ac/Approve_icon.svg/1200px-Approve_icon.svg.png'}
                    imageWidth={40}
                    imageHeight={40}
                    onClick={() => alert('Button pressed!')}
                />
                <hr color={'green'}/>

                <BitBtn
                    caption={'BitButton without Image'}
                    onClick={() => alert('Button pressed!')}
                />
            </ShowComponent>


            <ShowComponent name={'Memo'}>
                <Memo
                    lines={memo1}
                    rows={5}
                    cols={75}
                    onChange={(e) => setMemo1(e.currentTarget.value.split('\n'))}
                />
                <ShowResult result={memo1.join('\n')}/>
            </ShowComponent>


            <ShowComponent name={'ComboBox'}>
                <ComboBox
                    items={comboBoxItems1}
                    itemIndex={comboBoxItemIndex1}
                    width={200}
                    onChange={(e) => setComboBoxItemIndex1(+e.currentTarget.value)}
                />
                <ShowResult result={comboBoxItems1[comboBoxItemIndex1]}/>
                <hr color={'green'}/>

                <ComboBox
                    items={comboBoxItems2}
                    itemIndex={comboBoxItemIndex2}
                    width={200}
                    onChange={(e) => setComboBoxItemIndex2(+e.currentTarget.value)}
                />
                <ShowResult result={comboBoxItems2[comboBoxItemIndex2]}/>
            </ShowComponent>


            <ShowComponent name={'GroupBox'}>
                <GroupBox style={{width: '750px'}}>
                    <Edit
                        title={'GroupBox Test '}
                        text={'GroupBox Test'}
                    />
                    <BitBtn
                        caption={'GroupBox Test'}
                        onClick={() => alert('GroupBox Test!')}
                    />
                    <p/>
                    <Memo
                        lines={['GroupBox', 'Test']}
                    />
                </GroupBox>
            </ShowComponent>


        </div>
    );
}

export default App;
