import React, {DetailedHTMLProps, TextareaHTMLAttributes} from 'react'
import styles from './Memo.module.css'

// тип пропсов обычного инпута
type DefaultTextareaPropsType = DetailedHTMLProps<TextareaHTMLAttributes<HTMLTextAreaElement>, HTMLTextAreaElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type MemoPropsType = DefaultTextareaPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    lines?: Array<string>
}

export const Memo: React.FC<MemoPropsType> = (props) => {
    const {
        lines = [],
        value,
        ...restProps
    } = props

    return (
        <>
            <textarea
                className={styles.textarea}
                // value={lines.join('\r\n')}
                value={lines.join('\n')}
                {...restProps}
            />
        </>
    )
}