import React, {useState} from 'react'
import styles from './DatePickerTable.module.css'
import {DatePicker, getTitleDate, MAX_DATE, MIN_DATE} from '../DatePicker/DatePicker';


const MAX_Y_TABLE = 3
const MAX_X_TABLE = 4


type DatePickerTablePropsType = {
    initDate?: Date
    // initSize?: [1 | 2 | 3, 1 | 2 | 3 | 4]
    initSize?: [number, number]
    getInitSize?: (size: [number, number]) => void
    getDate?: (date: Date) => void
}

let key = 0

export const DatePickerTable: React.FC<DatePickerTablePropsType> = (props) => {

    const {
        initDate = new Date(Date.now()),
        initSize =
            props.initSize &&
            props.initSize[0] >= 1 && props.initSize[0] <= MAX_Y_TABLE &&
            props.initSize[1] >= 1 && props.initSize[1] <= MAX_X_TABLE
                ? props.initSize
                : [1, 1],
    } = props

    const [selectedDate, setSelectedDate] = useState<Date>(initDate)

    const [titleDate, setTitleDate] = useState<Date>(() => {
        const dayOfWeek = selectedDate.getDay()
        if (dayOfWeek &&
            selectedDate.getMonth() !== new Date(selectedDate.getFullYear(), selectedDate.getMonth(), selectedDate.getDate() + 7 - dayOfWeek).getMonth()) {
            return new Date(selectedDate.getFullYear(), selectedDate.getMonth() + 1, 1)
        }
        return new Date(selectedDate.getFullYear(), selectedDate.getMonth(), 1)
    })

    const [table, setTable] = useState(initSize)

    const createDatePickersTable = () => {
        let outerContent: Array<JSX.Element> = []

        for (let y = 0; y < table[0]; y++) {
            let innerContent: Array<JSX.Element> = []

            for (let x = 0; x < table[1]; x++) {
                const show = y === 0 && x === 0
                innerContent.push(
                    <td key={key++}>
                        <DatePicker
                            initDate={selectedDate}
                            getDate={(date) => {
                                setSelectedDate(date)
                                setTitleDate(getTitleDate(date))
                                props.getDate && props.getDate(date)
                            }}

                            initTitleDate={new Date(titleDate.getFullYear(), titleDate.getMonth() + y * table[1] + x, titleDate.getDate())}

                            changeTitleDate={(date) => setTitleDate(new Date(date.getFullYear(), date.getMonth() - (y * table[1] + x), 1))}

                            previousMonthButton={show}
                            previousMonthFunction={() => {
                                setTitleDate(date => new Date(date.getFullYear(), date.getMonth() - 1, 1) >= MIN_DATE
                                    ? new Date(date.setMonth(date.getMonth() - 1))
                                    : date)
                            }}
                            nextMonthButton={y === 0 && x === table[1] - 1}
                            nextMonthFunction={() =>
                                setTitleDate(date => new Date(date.getFullYear(), date.getMonth() + 1, 1) <= MAX_DATE
                                    ? new Date(date.setMonth(date.getMonth() + 1))
                                    : date)
                            }
                            showFooterButtons={show}
                            showFooterContent={show}
                            border={false}
                            monthBoardButtonLeft={() => setTable(state => {
                                const size = state[1] > 1 ? [state[0], (state[1] - 1)] : state
                                props.getInitSize && props.getInitSize(size as [number, number])
                                return size as [number, number]
                            })}
                            monthBoardButtonRight={() => setTable(state => {
                                const size = state[1] < MAX_X_TABLE ? [state[0], state[1] + 1] : state
                                props.getInitSize && props.getInitSize(size as [number, number])
                                return size as [number, number]
                            })}
                            monthBoardButtonUp={() => setTable(state => {
                                const size = state[0] > 1 ? [state[0] - 1, state[1]] : state
                                props.getInitSize && props.getInitSize(size as [number, number])
                                return size as [number, number]
                            })}
                            monthBoardButtonDown={() => setTable(state => {
                                const size = state[0] < MAX_Y_TABLE ? [state[0] + 1, state[1]] : state
                                props.getInitSize && props.getInitSize(size as [number, number])
                                return size as [number, number]
                            })}
                        />
                    </td>
                )

                if (table[1] > 1 && x < table[1] - 1) {
                    innerContent.push(<td key={key++} width={5}></td>)
                }
            }
            outerContent.push(<tr key={key++}>{innerContent}</tr>)
            if (table[0] > 1 && y < table[0] - 1) {
                outerContent.push(<tr key={key++} style={{height: '5px'}}></tr>)
            }
        }

        // return <table cellSpacing={0} cellPadding={0} style={{border: '1px solid green'}}>{outerContent}</table>
        // return <table cellSpacing={0} cellPadding={0} style={{border: '1px solid green'}}>{outerContent}</table>
        return outerContent
    }


    return (
        // <div className={styles.tableContent}>
        <table
            className={styles.tableContent}
            cellSpacing={0}
            cellPadding={0}
        >
            <tbody>
            {createDatePickersTable()}
            </tbody>
        </table>
        // </div>
    )
}