import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react'
import styles from './BitBtn.module.css'

// тип пропсов обычного инпута
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type BitBtnPropsType = DefaultButtonPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    caption: string
    image?: string
    imageWidth?: number
    imageHeight?: number
}

export const BitBtn: React.FC<BitBtnPropsType> = (props) => {
    const {
        caption,
        image,
        imageWidth = 20,
        imageHeight = 20,
        ...restProps
    } = props

    const imageStyle = {
        width: imageWidth,
        height: imageHeight,
    }

    return (
        <button
            {...restProps}
        >
            <div className={styles.block}>
                {image && <img
                    className={styles.image}
                    style={imageStyle}
                    src={image}
                    alt={'BitBtn'}
                />}
                {caption}
            </div>
        </button>
    )
}