import React, {DetailedHTMLProps, HTMLAttributes} from 'react'
import styles from './Label.module.css'

// тип пропсов обычного инпута
type DefaultLabelPropsType = DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>, HTMLSpanElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type LabelPropsType = DefaultLabelPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    caption: string
}

export const Label: React.FC<LabelPropsType> = (props) => {
    return (
        <span style={props.style}>
            {props.caption}
        </span>
    )
}