import React from 'react'
import styles from './ShowResult.module.css'


type PropsType = {
    result: string
}

export const ShowResult = (props: PropsType) => {
    return (
        <div className={styles.block}>
            <b>Result: </b>
            <span className={styles.resultText}>{props.result}</span>
        </div>
    )
}