import React, {useState} from 'react'
import styles from './DateEdit.module.css'
import {ModalWindowAtElement} from '../ModalWindowAtElement/ModalWindowAtElement';
import {DatePickerTable} from '../DatePickerTable/DatePickerTable';

/*
// тип пропсов обычного инпута
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type EditPropsType = DefaultInputPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    title?: string
    textAlignmentRight?: boolean
    text: string
}
*/

type DateEditPropsType = {
    initDate?: Date
    getDate?: (date: Date) => void
    initSize?: [number, number]
    showBorder?: boolean
}

export const DateEdit: React.FC<DateEditPropsType> = (props) => {
    const {
        initDate = new Date(Date.now()),
        initSize = props.initSize || [1, 1],
        showBorder = true,
    } = props

    const [date, setDate] = useState(initDate)
    const [size, setSize] = useState<[number, number]>(initSize)
    const [modalActive, setModalActive] = useState(false)

    const showDatePicker = () => {
        setModalActive(!modalActive)
    }

    return (
        <div>
            <div className={`${styles.container} ${showBorder ? styles.containerBorder : ''}`}>
                <input
                    className={styles.inputDate}
                    type={'text'}
                    placeholder={'date'}
                    maxLength={8}
                    value={date.toLocaleString('default', {day: '2-digit', month: '2-digit', year: '2-digit'})}
                />

                <button
                    onClick={showDatePicker}
                >
                    D
                </button>

            </div>

            <ModalWindowAtElement
                active={modalActive}
                onModalEntireWindowClick={() => setModalActive(false)}
            >
                <DatePickerTable
                    initDate={date}
                    getDate={(newDate) => {
                        setDate(newDate)
                        setModalActive(false)
                        props.getDate && props.getDate(newDate)
                    }}
                    initSize={size}
                    getInitSize={(size) => setSize(size)}
                />
            </ModalWindowAtElement>

        </div>
    )
}