import React, {useState} from 'react'
import styles from './DatePicker.module.css'
import {ModalWindowAtElement} from '../ModalWindowAtElement/ModalWindowAtElement';

/*
// тип пропсов обычного инпута
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтобы не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type EditPropsType = DefaultInputPropsType & { // и + ещё пропсы которых нет в стандартном инпуте
    title?: string
    textAlignmentRight?: boolean
    text: string
}
*/

type DatePickerPropsType = {
    initDate?: Date
    initTitleDate?: Date
    changeTitleDate?: (date: Date) => void
    getDate?: (date: Date) => void
    previousMonthFunction?: () => void
    nextMonthFunction?: () => void
    previousMonthButton?: boolean
    nextMonthButton?: boolean
    showFooterContent?: boolean
    showFooterButtons?: boolean
    monthBoardButtonUp?: () => void
    monthBoardButtonLeft?: () => void
    monthBoardButtonDown?: () => void
    monthBoardButtonRight?: () => void
    border?: boolean
}

const NUMBER_OF_4_WEEKS = 4
const NUMBER_OF_5_WEEKS = 5

const DAYS_OF_WEEK_RUS = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс']
const MONTHS_RUS = ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь']
const MIN_YEAR = 1950
const MAX_YEAR = 2099

export const MIN_DATE = new Date(MIN_YEAR, 0, 1)
export const MAX_DATE = new Date(MAX_YEAR, 11, 31)


const daysOfWeekFromArray = (arr: Array<string | number>) => {
    const res = arr.map((el, i) => <div key={i}>{el}</div>)

    return <div className={styles.bodyLine}>{res}</div>
}

export const getTitleDate = (date: Date) => {
    const dayOfWeek = date.getDay()
    if (dayOfWeek &&
        date.getMonth() !== new Date(date.getFullYear(), date.getMonth(), date.getDate() + 7 - dayOfWeek).getMonth()) {
        return new Date(date.getFullYear(), date.getMonth() + 1, 1)
    }
    return new Date(date.getFullYear(), date.getMonth(), 1)
}


export const DatePicker: React.FC<DatePickerPropsType> = (props) => {
    const {
        initDate = props.initDate && props.initDate >= MIN_DATE && props.initDate <= MAX_DATE
            ? props.initDate
            : new Date(Date.now()),
        initTitleDate,
        previousMonthButton = true,
        nextMonthButton = true,
        showFooterContent = true,
        showFooterButtons = false,
        border = true,
    } = props

    const today = new Date(Date.now())

    const [titleEditMode, setTitleEditMode] = useState<Boolean>(false)

    const [selectedDate, setSelectedDate] = useState<Date>(initDate)

    const [titleDate, setTitleDate] = useState<Date>(initTitleDate
        ? initTitleDate
        : getTitleDate(selectedDate))

    const firstDayOfMonth = new Date(titleDate.getFullYear(), titleDate.getMonth(), 1)
    const dayOfWeekOfFirstDayOfMonth = firstDayOfMonth.getDay() > 0 ? firstDayOfMonth.getDay() : 7

    const beginDate = new Date(titleDate.getFullYear(), titleDate.getMonth(), titleDate.getDate() - dayOfWeekOfFirstDayOfMonth + 1)

    const numberOfWeeks =
        titleDate.getMonth() === new Date(beginDate.getFullYear(), beginDate.getMonth(), beginDate.getDate() + NUMBER_OF_5_WEEKS * 7 - 1).getMonth()
            ? NUMBER_OF_5_WEEKS
            : NUMBER_OF_4_WEEKS


    const daysLinesFromDate = (beginDate: Date, numberOfWeeks: number) => {
        let date = beginDate
        let res: Array<JSX.Element> = []

        for (let a = 0; a < NUMBER_OF_5_WEEKS; a++) {
            let line = []

            for (let b = 0; b < 7; b++) {
                let lineElement: string | number
                if (a === NUMBER_OF_5_WEEKS - 1 && numberOfWeeks !== NUMBER_OF_5_WEEKS) {
                    lineElement = ''
                } else {
                    lineElement = date.getDate()
                }

                let lineElementClassName1 = lineElement && date.toDateString() === today.toDateString() && styles.lineElementToday
                let lineElementClassName2 = lineElement && date.toDateString() === selectedDate.toDateString() && styles.lineElementSelected

                const dateElement = new Date(date)

                line.push(
                    <div
                        key={a * 7 + b}
                        className={`${styles.lineElement} ${lineElementClassName1} ${lineElementClassName2}`}
                        onClick={() => lineElement && setNewSelectedDay(dateElement)}
                    >
                        {lineElement}
                    </div>
                )

                date.setDate(date.getDate() + 1)
            }

            res.push(<div className={styles.bodyLine} key={a}>{line}</div>)
        }

        return res
    }


    const setNewSelectedDay = (date: Date) => {
        setSelectedDate(date)
        props.getDate && props.getDate(date)
    }

// =========================================================
    const onTitleEditMode = () => {
        setTitleEditMode(true)
    }

    const offTitleEditMode = () => {
        setTitleEditMode(false)

                                                                    // переключение ЗАГОЛОВКА в календаре после редактирование заголовка
                                                                    // и клика в стороне
        props.changeTitleDate && props.changeTitleDate(titleDate)
    }

    const allMonths = (currentMonth: number) => {
        return (
            <select
                onChange={(e) => {
                    const date = new Date(titleDate.getFullYear(), +e.currentTarget.value, 1)
                    setTitleDate(date)
                                                                    // переключение ЗАГОЛОВКА в календаре после редактирование месяца в заголовке
                    // props.changeTitleDate && props.changeTitleDate(date)
                }}
            >
                {MONTHS_RUS.map((el, i) =>
                    <option key={i} selected={currentMonth === i} value={i}>{el}</option>)}
            </select>
        )
    }

    const allYears = (currentYear: number) => {
        let years: Array<JSX.Element> = []

        for (let i = MAX_YEAR; i >= MIN_YEAR; i--) {
            years.push(<option key={i} selected={currentYear === i} value={i}>{i}</option>)
        }

        return (
            <select
                onChange={(e) => {
                    const date = new Date(+e.currentTarget.value, titleDate.getMonth(), 1)
                    setTitleDate(date)
                                                                    // переключение ЗАГОЛОВКА в календаре после редактирование года в заголовке
                    // props.changeTitleDate && props.changeTitleDate(date)
                }}
            >
                {years}
            </select>
        )
    }
// =========================================================


    return (
        <div className={border ? styles.containerBorder : ''}>
            <div className={styles.header}>
                <div className={styles.titleButton}>
                    {previousMonthButton && <button
                        onClick={() => {
                            if (props.previousMonthFunction) {
                                props.previousMonthFunction()
                            } else {
                                setTitleDate(date => new Date(date.getFullYear(), date.getMonth() - 1, 1) >= MIN_DATE
                                    ? new Date(date.setMonth(date.getMonth() - 1))
                                    : date)
                            }
                        }}
                    >
                        {'<'}
                    </button>}
                </div>

                <div
                    className={styles.titleText}
                    onDoubleClick={onTitleEditMode}
                    // onBlur={offTitleEditMode}
                >
                    {!titleEditMode
                        ? `${titleDate.toLocaleString('default', {month: 'long'})}, ${titleDate.getFullYear()}`
                        : <div style={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            border: '1px solid red'
                        }}>
                            <ModalWindowAtElement
                                active={true}
                                onModalEntireWindowClick={offTitleEditMode}
                            >
                                {allMonths(titleDate.getMonth())} {allYears(titleDate.getFullYear())}
                            </ModalWindowAtElement>
                        </div>

                    }
                </div>

                <div className={styles.titleButton}>
                    {nextMonthButton && <button
                        onClick={() => {

                            if (props.nextMonthFunction) {
                                props.nextMonthFunction()
                            } else {
                                // setTitleDate(date => new Date(date.setMonth(date.getMonth() + 1)))
                                setTitleDate(date => new Date(date.getFullYear(), date.getMonth() + 1, 1) <= MAX_DATE
                                    ? new Date(date.setMonth(date.getMonth() + 1))
                                    : date)
                            }
                        }}
                    >
                        {'>'}
                    </button>}
                </div>
            </div>


            <div className={styles.body}>
                {daysOfWeekFromArray(DAYS_OF_WEEK_RUS)}
                <div>
                    <hr style={{margin: '1px 5px'}}/>
                </div>
                {daysLinesFromDate(beginDate, numberOfWeeks)}
            </div>


            <div className={styles.footer}>
                {showFooterContent && <div className={styles.currentDateTitle}
                                           onClick={() => {
                                               setNewSelectedDay(today)
                                               setTitleDate(getTitleDate(today))
                                           }}
                >
                    {`Today: ${today.toLocaleString('default', {month: 'long'})} ${today.getDate()}, ${today.getFullYear()}`}
                </div>}

                {showFooterButtons && <div className={styles.monthBoardButtons}>
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <button
                            className={styles.monthBoardButton}
                            onClick={() => props.monthBoardButtonUp && props.monthBoardButtonUp()}>
                            {'^'}
                        </button>
                    </div>
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <button
                            className={styles.monthBoardButton}
                            onClick={() => props.monthBoardButtonLeft && props.monthBoardButtonLeft()}>
                            {'<'}
                        </button>
                        <button
                            className={styles.monthBoardButton}
                            onClick={() => props.monthBoardButtonDown && props.monthBoardButtonDown()}>
                            {'v'}
                        </button>
                        <button
                            className={styles.monthBoardButton}
                            onClick={() => props.monthBoardButtonRight && props.monthBoardButtonRight()}>
                            {'>'}
                        </button>
                    </div>
                </div>}

            </div>
        </div>
    )
}