import React from 'react';
import styles from './ModalWindowAtElement.module.css'

type ModalWindowPropsType = {
    active: boolean
    // setActive: (active: boolean) => void
    onModalEntireWindowClick?: ()=>void
}

export const ModalWindowAtElement: React.FC<ModalWindowPropsType> = (props) => {
    if (props.active) {
        return (
            <>
                <div
                    className={styles.modalEntireWindow}
                    // onClick={()=>alert('Entire window!')}
                    onClick={props.onModalEntireWindowClick}
                >
                </div>

                <div
                    className={styles.modalContent}
                    // onClick={()=>alert('Modal content!')}
                >
                    {props.children}
                </div>
            </>
        )
    }

    return <></>
}